/*
==============================================================================================================
------------------------------------------ Шрифт «Roboto Condensed» ------------------------------------------
==============================================================================================================
*/
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local("Roboto Condensed Light"), local("RobotoCondensed-Light"), url("../fonts/RobotoCondensed-Light.woff2") format("woff2"), url("../fonts/RobotoCondensed-Light.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Roboto Condensed Regular"), local("RobotoCondensed-Regular"), url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Roboto Condensed Bold"), local("RobotoCondensed-Bold"), url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff"); }

/*
==============================================================================================================
---------------------------------------------- Шрифт «Bebas Neue» --------------------------------------------
==============================================================================================================
*/
@font-face {
  font-family: "Bebas Neue";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Bebas Neue Bold"), local("BebasNeue-Bold"), local("Bebas Neue"), local("BebasNeue"), url("../fonts/BebasNeue.woff2") format("woff2"), url("../fonts/BebasNeue.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  scroll-behavior: smooth; }

body {
  display: flex;
  flex-direction: column;
  width: 100vw;
  min-width: 320px;
  min-height: 100vh;
  font-family: "Roboto Condensed", Arial, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  color: #000000;
  overflow-x: hidden; }

footer {
  margin-top: auto; }

.visually-hidden {
  width: 1px;
  height: 1px;
  position: absolute;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* ***************************
*** 2. Универсальные стили ***
*************************** */
.container {
  margin: 0 auto;
  padding: 0; }

/* **********************
*** 3. Основные стили ***
********************** */
.site-header {
  outline: 2px solid red; }

.site-main-content {
  outline: 2px solid green; }

.site-footer {
  outline: 2px solid blue; }
